<template>
  <v-navigation-drawer app ref="rightDrawer" v-model="ui.drawer" :right="true">
    <v-app-bar elevation="0">
      <div class="nav-searcher">
        <v-text-field
          outlined
          dense
          hide-details="auto"
          append-icon="mdi-magnify"
        >
        </v-text-field>
      </div>
    </v-app-bar>
    <v-list class="nav-list" :dense="true">
      <v-list-item
        v-for="(nav, idx) in navs"
        :key="idx"
        @click.stop="navToHandler(nav)"
      >
        <v-list-item-content
          ><span class="nav-right-drawer">{{
            nav.text
          }}</span></v-list-item-content
        >
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import navs from '~/router/nav-menus'

export default {
  name: 'RightDrawer',
  components: {},
  data() {
    return {
      navs: navs
    }
  },
  computed: {
    // ...mapGetters('ui', ['rightDrawer'])
    ...mapState(['ui'])
  },
  methods: {
    navToHandler(nav) {
      this.$router.push({ path: nav.path }).then(() => {
        this.$store.dispatch('ui/toggleRightDrawer', false)
      })
    }
  }
}
</script>
<style lang="scss">
.nav-searcher {
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
