const navHome = {
  name: 'Home',
  text: 'Home',
  icon: 'mdi-wallet-giftcard',
  path: '/index',
  sort: 1,
  hidden: false
}

const rechargeNav = {
  name: 'Recharge',
  text: 'recharge',
  icon: 'mdi-wallet-membership',
  path: '/recharge',
  sort: 2,
  hidden: false
}

const allNavs = [navHome, rechargeNav]

export default allNavs
  .filter((n) => !Boolean(n.hidden))
  .sort((n1, n2) => {
    const s1 = n1.sort || 0,
      s2 = n2.sort || 0
    if (s1 > s2) return 1
    if (s1 < s2) return -1
    return 0
  })
