<template>
  <v-icon color="rgb(85, 85, 85)" @click="onBtnToggler">{{
    rightDrawer ? 'mdi-close' : 'mdi-menu'
  }}</v-icon>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'DrawerToggleBtn',
  components: {},
  data() {
    return {}
  },
  computed: {
    ...mapGetters('ui', ['rightDrawer'])
  },
  methods: {
    onBtnToggler() {
      this.$store.dispatch('ui/toggleRightDrawer')
    }
  }
}
</script>
<style scoped></style>
